body {
 @import url('https://fonts.googleapis.com/css?family=Roboto&display=swap');
 font-family: 'Roboto', sans-serif; }
a {
 color: darkblue; }

.wrapper {
 padding: 10px;
 max-width: 1024px;
 margin: 0 auto; }


.mainGrid {
 width: 600px;
 display: flex;
 align-items: center;  //по вертикали
 justify-content: center; // по горизонтали
 flex-direction: column; } // направление главной оси


.tabGrid {
 //padding: 10px
 display: flex;
 align-items: center;  //по вертикали
 justify-content: center; // по горизонтали
 flex-direction: row; // направление главной оси
 margin: 10px;
 height: 60px;
 width: 80%; }

.activeBtn {
 margin: 5%;
 background-color: blue;
 height: 50%;
 width: 30%;
 border-radius: 5px;
 color: white; }



.btn {
 margin: 5%;
 background-color: gray;
 height: 50%;
 width: 30%;
 border-radius: 5px;
 color: white; }

.films {
 width: 100%;
 height: auto;
 display: flex;
 align-items: center;  //по вертикали
 justify-content: center; // по горизонтали
 flex-direction: column; } // направление главной оси

.searchGrid {
 width: 80%;
 height: 10%;
 margin: 0;
 padding: 0; }


.input {
 width: 98%;
 height: 30px;
 font-size: large;
 border-radius: 7px;
 border: 2px solid black;
 margin: 0;
 padding: 5px; }

.filmsGrid {
 width: 80%;
 height: 50%;
 margin: 10px; }

.ul {

 display: flex;
 align-items: center;  //по вертикали
 justify-content: center; // по горизонтали
 flex-direction: column; // направление главной оси
 margin: 0;
 padding: 0;
 width: 100%; }


li {
 list-style-type: none; }


